function buildImageUrl(imageUrl: string | { url?: string } | null): string {
  if (!imageUrl) return ""
  const url = typeof imageUrl === "object" ? imageUrl.url : imageUrl
  if (!url) return ""
  if (url.startsWith("http")) return url
  return `${MEDICI_IMAGES_BASE}${url}`
}

function getMainImage(images: any[]): string {
  if (!images || images.length === 0) return ""
  const mainImage = images.find((img) => img.title === "mainimage")
  if (mainImage) return buildImageUrl(mainImage)
  return buildImageUrl(images[0])
}

function buildImageGallery(images: any[]): string[] {
  if (!images || images.length === 0) return []
  return images
    .slice(0, 10)
    .map((img) => buildImageUrl(img))
    .filter(Boolean)
}

/**
 * חיפוש מידע מקומי (אטרקציות, מסעדות, אירועים) עם Tavily
 */
async function searchLocalInfo(city: string, query: string): Promise<string | null> {
  if (!TAVILY_API_KEY) {
    console.log("[Tavily] API key not configured")
    return null
  }

  try {
    const response = await fetch("https://api.tavily.com/search", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        api_key: TAVILY_API_KEY,
        query: `${query} in ${city}`,
        search_depth: "basic",
        max_results: 5,
      }),
    })

    if (!response.ok) {
      console.log("[Tavily] Error:", response.status)
      return null
    }

    const data = await response.json()
    if (data.results && data.results.length > 0) {
      return data.results
        .map((r: any) => `${r.title}: ${r.content}`)
        .join("\n")
    }
    return null
  } catch (error) {
    console.error("[Tavily] Search error:", error)
    return null
  }
}

async function searchMediciHotels(params: {
  hotelName?: string
  city?: string
  dateFrom: string
  dateTo: string
  adults: number
  children: number[]
  filterHotelId?: string // Optional filter for specific hotel only
}) {
  console.log("[v0] Searching Medici API with params:", params)

  const url = `${MEDICI_API_BASE}/api/hotels/GetInnstantSearchPrice`

  const body: Record<string, any> = {
    dateFrom: params.dateFrom,
    dateTo: params.dateTo,
    pax: [
      {
        adults: params.adults || 2,
        children: params.children || [],
      },
    ],
    ShowExtendedData: true,
    limit: 10,
  }

  if (params.city) {
    body.city = params.city
  } else if (params.hotelName) {
    body.hotelName = params.hotelName
  } else {
    body.city = "Dubai"
  }

  console.log("[v0] Request body:", JSON.stringify(body, null, 2))

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${MEDICI_TOKEN}`,
      },
      body: JSON.stringify(body),
    })

    console.log("[v0] Response status:", response.status)

    if (!response.ok) {
      const errorText = await response.text()
      console.log("[v0] Error response:", errorText)
      throw new Error(`API error: ${response.status}`)
    }

    const data = await response.json()
    console.log("[v0] Response items count:", data?.items?.length || 0)

    // Filter results by hotel ID if filterHotelId is provided
    if (params.filterHotelId && data?.items) {
      data.items = data.items.filter((hotel: any) => 
        hotel.hotelId?.toString() === params.filterHotelId
      )
      console.log(`[v0] Filtered to hotel ID ${params.filterHotelId}, remaining items:`, data.items.length)
    }

    return {
      results: data,
      jsonRequest: JSON.stringify(body),
    }
  } catch (error) {
    console.error("[v0] Search error:", error)
    throw error
  }
}

async function prebookRoom(params: {
  code: string
  hotelId: number
  dateFrom: string
  dateTo: string
  adults: number
  children: number[]
  searchRequestJson: string
}) {
  console.log("[v0] PreBook with params:", params)

  const url = `${MEDICI_API_BASE}/api/hotels/PreBook`

  // Parse the original search request to build PreBook request
  let searchRequest
  try {
    searchRequest = JSON.parse(params.searchRequestJson)
  } catch (error) {
    console.error("[v0] Failed to parse search request:", error)
    throw new Error("Invalid search request JSON")
  }

  // Build PreBook request according to Medici API format
  const prebookRequest = {
    services: [
      {
        searchCodes: [
          {
            code: params.code,
            pax: [
              {
                adults: params.adults,
                children: params.children,
              },
            ],
          },
        ],
        searchRequest: searchRequest,
      },
    ],
  }

  const body = {
    jsonRequest: JSON.stringify(prebookRequest),
  }

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${MEDICI_TOKEN}`,
      },
      body: JSON.stringify(body),
    })

    if (!response.ok) {
      const errorText = await response.text()
      console.log("[v0] PreBook error:", errorText)
      throw new Error(`PreBook error: ${response.status}`)
    }

    const data = await response.json()
    console.log("[v0] PreBook success:", data)
    return data
  } catch (error) {
    console.error("[v0] PreBook error:", error)
    throw error
  }
}

async function bookRoom(params: {
  token: string
  preBookId: string
  customer: {
    firstName: string
    lastName: string
    email: string
    phone: string
  }
  searchRequestJson: string
  roomCode: string
  adults: number
  children: number[]
}) {
  console.log("[v0] Book with params:", params)

  const url = `${MEDICI_API_BASE}/api/hotels/Book`

  // Parse search request
  let searchRequest
  try {
    searchRequest = JSON.parse(params.searchRequestJson)
  } catch (error) {
    console.error("[v0] Failed to parse search request:", error)
    throw new Error("Invalid search request JSON")
  }

  // Build adult guests array
  const adultGuests = []
  for (let i = 0; i < params.adults; i++) {
    adultGuests.push({
      title: "MR",
      name: {
        first: i === 0 ? params.customer.firstName : `Guest${i + 1}`,
        last: params.customer.lastName,
      },
      birthDate: "1990-01-01",
    })
  }

  // Build Book request according to Medici API format
  const bookRequest = {
    customer: {
      title: "MR",
      name: {
        first: params.customer.firstName,
        last: params.customer.lastName,
      },
      birthDate: "1990-01-01",
      contact: {
        email: params.customer.email,
        phone: params.customer.phone,
      },
    },
    paymentMethod: {
      methodName: "account_credit",
    },
    reference: {
      agency: "v0-bookinengine-ai-chat",
      voucherEmail: params.customer.email,
    },
    services: [
      {
        bookingRequest: [
          {
            code: params.roomCode,
            pax: [
              {
                adults: adultGuests,
                children: [],
              },
            ],
            token: params.token,
          },
        ],
        searchRequest: searchRequest,
      },
    ],
  }

  const body = {
    jsonRequest: JSON.stringify(bookRequest),
  }

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${MEDICI_TOKEN}`,
      },
      body: JSON.stringify(body),
    })

    if (!response.ok) {
      const errorText = await response.text()
      console.log("[v0] Book error:", errorText)
      throw new Error(`Book error: ${response.status}`)
    }

    const data = await response.json()
    console.log("[v0] Book success:", data)
    return data
  } catch (error) {
    console.error("[v0] Book error:", error)
    throw error
  }
}

export async function POST(req: Request) {
  try {
    const { messages, hotelConfig, language, bookingState, sessionId } = (await req.json()) as {
      messages: { role: "user" | "assistant"; content: string }[]
      hotelConfig: HotelConfig
      language: "he" | "en"
      sessionId?: string
      bookingState?: {
        step?: "search" | "select" | "prebook" | "details" | "book"
        selectedRoom?: any
        jsonRequest?: string
        preBookData?: any
        searchContext?: any
      }
    }

    // Generate or use provided session ID
    const currentSessionId = sessionId || `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
    
    // Initialize memory for this session
    const memory = ChatMemoryService.getOrCreateMemory(currentSessionId, hotelConfig?.id || 'default')
    
    // Get last user message
    const lastUserMessage = messages[messages.length - 1]
    if (lastUserMessage?.role === 'user') {
      // Add user message to memory
      ChatMemoryService.addMessage(currentSessionId, 'user', lastUserMessage.content)
      
      // Check for admin trigger
      if (ChatMemoryService.isAdminTrigger(lastUserMessage.content)) {
        ChatMemoryService.activateAdminMode(currentSessionId)
        
        return Response.json({
          text: `🔐 **מצב מנהל הופעל!**

שלום מנהל המלון! עכשיו אתה יכול:

1. **לעדכן הנחיות מערכת** - רק תגיד:
   - "הנחיות חדשות: [ההנחיות שלך]"
   - "עדכן הנחיות: [ההנחיות החדשות]"
   
2. **לשנות התנהגות ה-AI** - למשל:
   - "הנחיות חדשות: תהיה יותר פורמלי ומקצועי"
   - "עדכן הנחיות: תן דגש על המבצעים והקופונים"
   - "הנחיות חדשות: תהיה יותר רומנטי וחם בתגובות"

3. **ההנחיות ישמרו** למשך כל השיחה ויחולו על כל התגובות הבאות.

מה תרצה לעדכן?`,
          sessionId: currentSessionId,
          adminMode: true
        })
      }
      
      // Check if in admin mode and extracting instructions
      if (ChatMemoryService.isAdminMode(currentSessionId)) {
        const extractedInstructions = ChatMemoryService.extractAndSaveInstructions(
          currentSessionId, 
          lastUserMessage.content
        )
        
        if (extractedInstructions) {
          return Response.json({
            text: `✅ **הנחיות עודכנו בהצלחה!**

📝 ההנחיות החדשות:
"${extractedInstructions}"

ההנחיות האלה יחולו על כל התגובות שלי מעכשיו. אני אתאים את הסגנון, הטון והתוכן שלי בהתאם.

רוצה לבדוק? שאל אותי שאלה והראה לך איך אני משתמש בהנחיות החדשות! 🚀`,
            sessionId: currentSessionId,
            adminMode: true,
            customInstructions: extractedInstructions
          })
        }
      }
    }

    const isHebrew = language === "he"
    const hotelName = hotelConfig?.name || "Dizengoff Inn"
    const hotelApiName = hotelConfig?.apiSettings?.mediciHotelName || DEFAULT_HOTEL_NAME
    const hotelCity = hotelConfig?.apiSettings?.mediciCity || hotelConfig?.city || "Tel Aviv"

    console.log("[v0] Chat request - Hotel:", hotelName, "API Name:", hotelApiName)
    console.log("[v0] Session ID:", currentSessionId)
    console.log("[v0] Admin Mode:", ChatMemoryService.isAdminMode(currentSessionId))
    console.log("[v0] Booking state:", bookingState)

    const today = new Date().toISOString().split("T")[0]

    // Get base system prompt
    let systemPrompt = getBookingAgentPrompt(
      language, 
      hotelName, 
      hotelCity, 
      today,
      hotelConfig?.id,
      hotelConfig?.knowledgeBase,
      hotelConfig?.systemInstructions
    )
    
    // Add Scarlet-specific knowledge
    if (hotelConfig?.id === "scarlet-hotel") {
      const kb = scarletKnowledgeBase
      systemPrompt += `\n\n## 🏨 מידע מפורט על מלון סקרלט תל אביב:\n\n`
      
      systemPrompt += `**זהות המלון:**\n`
      systemPrompt += `- שם: ${kb.identity.name} (${kb.identity.englishName})\n`
      systemPrompt += `- סלוגן: ${kb.identity.tagline}\n`
      systemPrompt += `- קונצפט: ${kb.identity.concept}\n`
      systemPrompt += `- קהל יעד: ${kb.identity.targetAudience}\n\n`
      
      systemPrompt += `**נקודות מכירה ייחודיות:**\n`
      kb.identity.uniqueSellingPoints.forEach((point: string) => {
        systemPrompt += `- ${point}\n`
      })
      systemPrompt += `\n`
      
      systemPrompt += `**מיקום:**\n`
      systemPrompt += `- כתובת: ${kb.location.address}\n`
      systemPrompt += `- שכונה: ${kb.location.neighborhood}\n`
      systemPrompt += `- אטרקציות בקרבת מקום:\n`
      kb.location.nearbyAttractions.slice(0, 5).forEach((attr: any) => {
        systemPrompt += `  * ${attr.name} - ${attr.distance} (${attr.description})\n`
      })
      systemPrompt += `\n`
      
      systemPrompt += `**7 סוגי חדרים:**\n`
      kb.rooms.forEach((room: any) => {
        systemPrompt += `\n**${room.name}** (${room.englishName})\n`
        systemPrompt += `- גודל: ${room.size}\n`
        systemPrompt += `- תיאור: ${room.description}\n`
        systemPrompt += `- מקסימום אורחים: ${room.maxGuests}\n`
        systemPrompt += `- טווח מחירים: ${room.priceRange}\n`
        if (room.detailedFeatures) {
          systemPrompt += `- מיטה: ${room.detailedFeatures.bedding}\n`
          systemPrompt += `- חדר רחצה: ${room.detailedFeatures.bathroom}\n`
          systemPrompt += `- נוף: ${room.detailedFeatures.view}\n`
        }
      })
      systemPrompt += `\n`
      
      systemPrompt += `**שירותים:**\n`
      systemPrompt += `- דלפק קבלה: ${kb.services.frontDesk.hours}\n`
      systemPrompt += `- שפות: ${kb.services.frontDesk.languages.join(", ")}\n`
      systemPrompt += `- שירותים מיוחדים: ${kb.services.specialServices.join(", ")}\n\n`
      
      systemPrompt += `**מדיניות:**\n`
      systemPrompt += `- צ'ק-אין: ${kb.policies.checkIn}\n`
      systemPrompt += `- צ'ק-אאוט: ${kb.policies.checkOut}\n`
      systemPrompt += `- ביטול: ${kb.policies.cancellation.join("; ")}\n\n`
      
      systemPrompt += `**יתרונות תחרותיים:**\n`
      kb.competitiveAdvantages.forEach((adv: any) => {
        systemPrompt += `- ${adv.advantage}: ${adv.description}\n`
      })
      systemPrompt += `\n`
      
      systemPrompt += `**הנחיות תקשורת:**\n`
      systemPrompt += `- ${kb.agentInstructions.tone}\n`
      systemPrompt += `- ${kb.agentInstructions.priorities}\n`
      systemPrompt += `- ${kb.agentInstructions.upselling}\n`
      systemPrompt += `- ${kb.agentInstructions.objectionHandling}\n\n`
      
      systemPrompt += `**טיפים חשובים:**\n`
      kb.agentInstructions.keyTips.forEach((tip: string) => {
        systemPrompt += `✨ ${tip}\n`
      })
      
      console.log("[Scarlet] Enhanced system prompt with full knowledge base")
    }
    
    // Add custom instructions if in admin mode
    const customInstructions = ChatMemoryService.getCustomInstructions(currentSessionId)
    if (customInstructions) {
      systemPrompt += `\n\n## 🔐 הנחיות מותאמות אישית ממנהל המלון (עדיפות גבוהה!):\n${customInstructions}\n\nהנחיות אלה חשובות ביותר ודורסות הנחיות אחרות במקרה של סתירה.`
      console.log("[v0] Applied custom instructions from admin")
    }
    
    // Add conversation history context
    const conversationSummary = ChatMemoryService.getSummary(currentSessionId)
    systemPrompt += `\n\n## 💬 הקשר שיחה:\n${conversationSummary}`

    console.log("[v0] Calling AI model (Groq)...")

    // Use Groq API for chat completion
    const chatCompletion = await groq.chat.completions.create({
      model: "llama-3.3-70b-versatile",
      messages: [
        { role: "system", content: systemPrompt },
        ...messages.map((m) => ({
          role: m.role as "user" | "assistant",
          content: m.content,
        })),
      ],
      temperature: 0.7,
      max_tokens: 2048,
    })

    const text = chatCompletion.choices[0]?.message?.content || ""

    console.log("[v0] AI response:", text.slice(0, 500))
    
    // Add AI response to memory
    ChatMemoryService.addMessage(currentSessionId, 'assistant', text)

    const searchMatch = text.match(/\[SEARCH\](.*?)\[\/SEARCH\]/s)
    if (searchMatch) {
      console.log("[v0] Found search request:", searchMatch[1])

      try {
        const searchParams = JSON.parse(searchMatch[1])
        console.log("[v0] Parsed search params:", searchParams)

        // For Scarlet Hotel, prioritize Static Data API for accurate room info
        let scarletStaticRooms: any[] = []
        if (hotelConfig?.id === "scarlet-hotel") {
          try {
            const staticResponse = await fetch(`${STATIC_DATA_API}/hotels/863233`, {
              headers: {
                "Authorization": `Bearer ${STATIC_DATA_KEY}`,
              },
            })
            if (staticResponse.ok) {
              const staticData = await staticResponse.json()
              console.log("[Scarlet] Static Data API response:", staticData)
              scarletStaticRooms = staticData.rooms || []
            }
          } catch (error) {
            console.log("[Scarlet] Static Data API error:", error)
          }
        }

        const { results: searchResults, jsonRequest } = await searchMediciHotels({
          hotelName: hotelApiName,
          city: searchParams.city || hotelCity,
          dateFrom: searchParams.dateFrom,
          dateTo: searchParams.dateTo,
          adults: searchParams.adults || 2,
          children: searchParams.children || [],
          filterHotelId: hotelConfig?.id === "scarlet-hotel" ? "863233" : undefined, // Filter Scarlet only
        })

        let rooms: any[] = []

        if (searchResults?.items && Array.isArray(searchResults.items)) {
          rooms = searchResults.items
        } else if (Array.isArray(searchResults)) {
          rooms = searchResults
        } else if (searchResults?.hotels) {
          rooms = searchResults.hotels
        }

        if (rooms.length > 0) {
          const formattedRooms = rooms.slice(0, 6).map((room: any, index: number) => {
            const price = room.price?.amount || room.netPrice?.amount || room.price || 0
            const currency = room.price?.currency || room.netPrice?.currency || "USD"
            const rawImages = room.images || []
            const mainImage = getMainImage(rawImages)
            const imageGallery = buildImageGallery(rawImages)
            const facilities = room.facilities?.tags || room.facilities?.list || []

            return {
              code: room.code || `${room.hotelId || hotelName}:${index}:${Date.now()}`,
              hotelId: room.hotelId || 0,
              name: room.hotelName || room.name || "Hotel",
              hotelName: room.hotelName || hotelName,
              roomType: room.name || room.category || "Standard Room",
              board: room.board || "RO",
              price: price,
              currency: currency,
              cancellation: room.cancellation?.type || "non-refundable",
              confirmation: room.confirmation || "immediate",
              image: mainImage,
              images: imageGallery,
              description: room.description || "",
              facilities: facilities,
              location: room.city || searchParams.city || hotelCity,
              address: room.address || "",
              rating: room.stars || 4,
            }
          })

          const cleanText = text.replace(/\[SEARCH\].*?\[\/SEARCH\]/s, "").trim()

          const roomsList = formattedRooms
            .map((r, i) => {
              const label =
                i === 0
                  ? isHebrew
                    ? " (הכי משתלם)"
                    : " (Best Value)"
                  : i === formattedRooms.length - 1
                    ? isHebrew
                      ? " (הכי גמיש)"
                      : " (Most Flexible)"
                    : ""

              return isHebrew
                ? `${i + 1}. ${r.hotelName} - ${r.roomType}${label}\n   מחיר: $${r.price} ${r.currency}\n   קוד חדר: ${r.code}\n   ביטול: ${r.cancellation === "fully-refundable" ? "ניתן לביטול חינם" : "לא ניתן לביטול"}`
                : `${i + 1}. ${r.hotelName} - ${r.roomType}${label}\n   Price: $${r.price} ${r.currency}\n   Room code: ${r.code}\n   Cancellation: ${r.cancellation === "fully-refundable" ? "Free cancellation" : "Non-refundable"}`
            })
            .join("\n\n")

          // Log the search
          SearchLogger.logSearch({
            hotelName: hotelApiName,
            city: searchParams.city || hotelCity,
            dateFrom: searchParams.dateFrom,
            dateTo: searchParams.dateTo,
            adults: searchParams.adults || 2,
            children: searchParams.children || 0,
            resultsCount: formattedRooms.length,
            foundHotels: rooms.length,
            foundRooms: formattedRooms.length,
            success: true,
            source: "chat",
            channel: "web",
            metadata: {
              hotelId: hotelConfig?.id,
              userMessage: messages[messages.length - 1]?.content.substring(0, 100),
            }
          }).catch(err => console.error("Failed to log search:", err))

          return Response.json({
            message:
              cleanText +
              "\n\n" +
              (isHebrew
                ? `מצאתי ${formattedRooms.length} אפשרויות זמינות:\n\n${roomsList}\n\nאיזה חדר מעניין אותך? כתוב את המספר או "אני רוצה את חדר מספר X"`
                : `I found ${formattedRooms.length} available options:\n\n${roomsList}\n\nWhich room interests you? Write the number or "I want room number X"`),
            bookingData: {
              type: "search_results",
              data: {
                rooms: formattedRooms,
                searchContext: {
                  dateFrom: searchParams.dateFrom,
                  dateTo: searchParams.dateTo,
                  adults: searchParams.adults || 2,
                  children: searchParams.children || [],
                  city: searchParams.city || hotelCity,
                },
                jsonRequest: jsonRequest,
              },
            },
          })
        }
      } catch (error) {
        console.error("[v0] Search error:", error)
        
        // Log the failed search
        SearchLogger.logSearch({
          hotelName: hotelApiName,
          city: searchParams?.city || hotelCity,
          dateFrom: searchParams?.dateFrom,
          dateTo: searchParams?.dateTo,
          adults: searchParams?.adults || 2,
          children: searchParams?.children || 0,
          resultsCount: 0,
          success: false,
          errorMessage: error instanceof Error ? error.message : "Unknown search error",
          source: "chat",
          channel: "web",
          metadata: {
            hotelId: hotelConfig?.id,
            error: error instanceof Error ? error.toString() : String(error),
          }
        }).catch(err => console.error("Failed to log search error:", err))
        
        const cleanText = text.replace(/\[SEARCH\].*?\[\/SEARCH\]/s, "").trim()
        return Response.json({
          message:
            cleanText +
            "\n\n" +
            (isHebrew
              ? "הייתה בעיה ביצירת קשר עם מערכת ההזמנות. אנא נסה שוב."
              : "There was an issue contacting the booking system. Please try again."),
        })
      }
    }

    const selectMatch = text.match(/\[SELECT_ROOM\](.*?)\[\/SELECT_ROOM\]/s)
    if (selectMatch && bookingState?.jsonRequest) {
      console.log("[v0] Room selected, calling PreBook...")

      try {
        const selection = JSON.parse(selectMatch[1])
        const preBookData = await prebookRoom({
          code: selection.code,
          hotelId: selection.hotelId,
          dateFrom: bookingState.searchContext.dateFrom,
          dateTo: bookingState.searchContext.dateTo,
          adults: bookingState.searchContext.adults,
          children: bookingState.searchContext.children,
          searchRequestJson: bookingState.jsonRequest,
        })

        const cleanText = text.replace(/\[SELECT_ROOM\].*?\[\/SELECT_ROOM\]/s, "").trim()

        return Response.json({
          message:
            cleanText +
            "\n\n" +
            (isHebrew
              ? `מעולה! שמרתי את החדר עבורך.\nעכשיו אני צריך כמה פרטים:\n- שם מלא\n- דוא"ל\n- מספר טלפון`
              : `Great! I've reserved the room for you.\nNow I need some details:\n- Full name\n- Email\n- Phone number`),
          bookingData: {
            type: "prebook_complete",
            data: {
              preBookData: preBookData,
              selectedRoom: selection,
            },
          },
        })
      } catch (error) {
        console.error("[v0] PreBook error:", error)
        return Response.json({
          message: isHebrew
            ? "הייתה בעיה בשמירת החדר. אנא נסה שוב או בחר חדר אחר."
            : "There was an issue reserving the room. Please try again or select another room.",
        })
      }
    }

    const bookMatch = text.match(/\[BOOK\](.*?)\[\/BOOK\]/s)
    if (bookMatch && bookingState?.preBookData && bookingState?.selectedRoom && bookingState?.jsonRequest) {
      console.log("[v0] Completing booking...")

      try {
        const customerDetails = JSON.parse(bookMatch[1])
        const bookingResult = await bookRoom({
          token: bookingState.preBookData.token,
          preBookId: bookingState.preBookData.preBookId,
          customer: customerDetails,
          searchRequestJson: bookingState.jsonRequest,
          roomCode: bookingState.selectedRoom.code,
          adults: bookingState.searchContext?.adults || 2,
          children: bookingState.searchContext?.children || [],
        })

        // Send confirmation email (non-blocking)
        if (bookingResult.bookingId && bookingResult.supplierReference && emailService.isEnabled()) {
          try {
            const searchRequest = JSON.parse(bookingState.jsonRequest)
            const checkInDate = searchRequest.dates?.from || new Date().toISOString()
            const checkOutDate = searchRequest.dates?.to || new Date().toISOString()
            const nights = Math.ceil(
              (new Date(checkOutDate).getTime() - new Date(checkInDate).getTime()) / (1000 * 60 * 60 * 24)
            )

            emailService
              .sendBookingConfirmation({
                to: customerDetails.email,
                customerName: `${customerDetails.firstName} ${customerDetails.lastName}`,
                bookingId: bookingResult.bookingId,
                supplierReference: bookingResult.supplierReference,
                hotelName: bookingState.selectedRoom?.hotelName || hotelName,
                roomType: bookingState.selectedRoom?.roomName || "Room",
                checkIn: format(new Date(checkInDate), "MMM dd, yyyy"),
                checkOut: format(new Date(checkOutDate), "MMM dd, yyyy"),
                nights,
                adults: bookingState.searchContext?.adults || 2,
                children: bookingState.searchContext?.children?.length || 0,
                totalPrice: bookingState.preBookData?.netPrice || 0,
                currency: searchRequest.currencies?.[0] || "USD",
                language: language,
              })
              .then((emailResult) => {
                if (emailResult.success) {
                  console.log("[AI Chat] ✅ Confirmation email sent", {
                    bookingId: bookingResult.bookingId,
                    emailId: emailResult.emailId,
                  })
                } else {
                  console.warn("[AI Chat] ⚠️ Email failed (non-critical)", emailResult.error)
                }
              })
              .catch((error) => {
                console.error("[AI Chat] Email error (non-critical):", error)
              })
          } catch (error) {
            console.error("[AI Chat] Failed to parse search request for email:", error)
          }
        }

        const cleanText = text.replace(/\[BOOK\].*?\[\/BOOK\]/s, "").trim()

        return Response.json({
          message:
            cleanText +
            "\n\n" +
            (isHebrew
              ? `🎉 ההזמנה הושלמה בהצלחה!\n\nמספר הזמנה: ${bookingResult.bookingId}\nאסמכתא: ${bookingResult.supplierReference}\n\nקיבלת אישור במייל ${customerDetails.email}`
              : `🎉 Booking completed successfully!\n\nBooking ID: ${bookingResult.bookingId}\nReference: ${bookingResult.supplierReference}\n\nYou've received confirmation at ${customerDetails.email}`),
          bookingData: {
            type: "booking_complete",
            data: {
              bookingId: bookingResult.bookingId,
              supplierReference: bookingResult.supplierReference,
            },
          },
        })
      } catch (error) {
        console.error("[v0] Book error:", error)
        return Response.json({
          message: isHebrew
            ? "הייתה בעיה בסיום ההזמנה. אנא נסה שוב או צור קשר עם התמיכה."
            : "There was an issue completing the booking. Please try again or contact support.",
        })
      }
    }

    const cleanText = text
      .replace(/\[SEARCH\].*?\[\/SEARCH\]/s, "")
      .replace(/\[SELECT_ROOM\].*?\[\/SELECT_ROOM\]/s, "")
      .replace(/\[BOOK\].*?\[\/BOOK\]/s, "")
      .trim()

    // Return response with session ID and memory info
    return Response.json({ 
      message: cleanText,
      sessionId: currentSessionId,
      hasMemory: memory.messages.length > 0,
      messageCount: memory.metadata.messageCount,
      adminMode: ChatMemoryService.isAdminMode(currentSessionId)
    })
  } catch (error) {
    console.error("[v0] AI Chat error:", error)
    return Response.json(
      {
        message: "Sorry, an error occurred. Please try again.",
      },
      { status: 500 },
    )
  }
}
